/*----------------------------------------
  #   FONT SIZES
  This is a Major Third scale that powers all the utilities that it is
  relevant for FONT SIZES All items are calculated off the base size,
  so change that and cascade across your whole project.
----------------------------------------*/
:root {
  --font-ratio: 1.2;
  --f0: 1rem;
  --f1: calc(var(--f0) * var(--font-ratio));
  --f2: calc(var(--f1) * var(--font-ratio));
  --f3: calc(var(--f2) * var(--font-ratio));
  --f4: calc(var(--f3) * var(--font-ratio));
  --f5: calc(var(--f4) * var(--font-ratio));
  --f6: calc(var(--f5) * var(--font-ratio));
  --f7: calc(var(--f6) * var(--font-ratio));
  --f8: calc(var(--f7) * var(--font-ratio));
  --heading-style: Sans-Serif;
  --body-style: Sans-Serif;
  --heading-font: "Lato", var(--heading-style);
  --body-font: "Lato", var(--body-style);
  --heading-weight: 800;
  --body-weight: 300;
  --body-line-height: 2;
  --heading-line-height: 1.2;
  --max-character-length: 15ch;
}

body {
  font-family: var(--body-font);
  font-weight: var(--body-weight);
  font-size: var(--f0);
  line-height: var(--body-line-height);
}
@media only screen and (max-width: 767px) {
  body {
    --body-line-height: 1.2;
  }
}

.heading-font {
  font-family: var(--heading-font);
}

.body-font {
  font-family: var(--body-font);
}

h1, h2 {
  font-weight: var(--heading-weight);
  max-width: var(--max-character-length);
}

h3, h4, h5, h6 {
  font-weight: var(--body-weight);
}

h1, h2, h3, h4, h5, h6 {
  min-height: 0.000001vw;
  color: var(--base-heading);
  margin-bottom: var(--f2);
  font-family: var(--heading-font) !important;
  line-height: var(--heading-line-height);
}

#george-block h1, #george-block .h1 {
  font-size: clamp(var(--f5), 10vw, var(--f7)) !important;
}
