  font-size: clamp(var(--f2), 8vw, var(--f4));
}
#george-block h3, #george-block .h3 {
  font-size: clamp(var(--f1), 5vw, var(--f3));
}
#george-block h4, #george-block .h4 {
  font-size: clamp(var(--f0), 3vw, var(--f2));
}
#george-block h5, #george-block .h5 {
  font-size: clamp(var(--f0), 2vw, var(--f1));
}
#george-block h6, #george-block .h6 {
  font-size: clamp(var(--f0), 1vw, var(--f0));
  font-weight: 100;
}

html.safari-12 h1, html.safari-12 .h1, html.safari-13 h1, html.safari-13 .h1 {
  font-size: min(max(var(--f3), 10vw), var(--f5));
}
html.safari-12 h2, html.safari-12 .h2, html.safari-13 h2, html.safari-13 .h2 {
  font-size: min(max(var(--f2), 8vw), var(--f4));
}
html.safari-12 h3, html.safari-12 .h3, html.safari-13 h3, html.safari-13 .h3 {
  font-size: min(max(var(--f1), 5vw), var(--f3));
}
html.safari-12 h4, html.safari-12 .h4, html.safari-13 h4, html.safari-13 .h4 {
  font-size: clamp(var(--f0), 3vw, var(--f2));
}
html.safari-12 h5, html.safari-12 .h5, html.safari-13 h5, html.safari-13 .h5 {
  font-size: clamp(var(--f0), 2vw, var(--f1));
}
html.safari-12 h6, html.safari-12 .h6, html.safari-13 h6, html.safari-13 .h6 {
  font-size: clamp(var(--f0), 1vw, var(--f0));
}

a {
  color: var(--base-link-colour);
}

p {
  font-size: clamp(var(--f0), 2vw, var(--f0));
  margin-bottom: var(--f1);
  color: var(--base-body);
}
p.intro {
  font-size: min(max(var(--f1), 2vw), var(--f1));
  line-height: 1.2;
}

p[style*="text-align: center"] {
  max-width: unset;
}

ol, ul {
  color: var(--base-body);
  padding-left: 13px;
}
ol li, ul li {
  color: var(--base-body);
}

blockquote {
  font-size: clamp(var(--f2), 5vw, var(--f4));
  color: var(--base-heading);
  font-family: var(--heading-font);
  font-weight: 800;
  line-height: 1.2em;
  font-style: italic;
  text-align: left;
  padding: var(--s2) var(--s-1);
}
@media only screen and (max-width: 767px) {
  blockquote {
    padding: var(--s2) 0;
  }
}

strong {
  font-weight: 600;
}

cite {
  color: var(--tertiary-main) !important;
  font-family: var(--body-font);
  font-style: normal;
  font-size: var(--f1);
  margin-bottom: 0;
}
cite + p {
  margin-top: 0 !important;
  max-width: unset;
}

hr {
  border-top: var(--s-6) solid var(--base-grey);
  width: 100%;
}

a {
  text-decoration: underline;
}

address {
  font-style: normal;
}

p + p .button, p + p input[type=submit] {
  margin-top: var(--s-4);
}

.button, input[type=submit], button {
  align-self: start;
  cursor: pointer;
  display: inline-block;
  font-family: var(--body-font);
  font-size: var(--s1);
  font-weight: var(--link-weight);
  padding: var(--s-1) var(--s2);
  text-align: center;
  text-decoration: none;
  transition: all ease-in 200ms;
  line-height: 1.5;
  margin-right: var(--s-2);
  margin-bottom: var(--s-2);
  border-color: transparent;
  background-size: 100% 100%;
  border-radius: var(--link-radius);
  /* Button styles */
  /*
  - These classes control the look of buttons
    and also set the base colours from variables.

  - These colours are affected by background colours if set,
    these styles are located in _utilities/_generator/_colour-utility.scss
  */
}
.button.primary, input[type=submit].primary, button.primary {
  background-color: var(--base-button-background);
  border: var(--base-button-border-weight) solid var(--base-button-border-color);
  color: var(--base-button-text);
}
.button.primary:hover, input[type=submit].primary:hover, button.primary:hover {
  background-color: var(--base-button-hover);
  border-color: var(--base-button-hover-border-color);
  color: var(--base-button-hover-text);
  transition: all ease-in 200ms;
  text-decoration: none;
}
.button.primary:after, input[type=submit].primary:after, button.primary:after {
  content: "";
}
.button.ghost, input[type=submit].ghost, button.ghost {
  align-self: start;
  background-color: transparent;
  background-size: 100% 100%;
  border: var(--base-button-border-weight) solid var(--base-button-background);
  border-radius: var(--link-radius);
  color: var(--base-button-background);
  cursor: pointer;
  border-color: inherit;
  display: inline-block;
  box-sizing: content-box;
}
.button.ghost:hover, input[type=submit].ghost:hover, button.ghost:hover {
  border-color: var(--base-button-hover-border-color);
  background-color: var(--base-button-background);
  color: var(--base-button-hover-text);
  transition: background-color ease-in 200ms;
}
.button.ghost.white, input[type=submit].ghost.white, button.ghost.white {
  border-color: var(--light-main);
  color: var(--light-main);
}
.button.secondary, input[type=submit].secondary, button.secondary {
  display: inline-block;
  background-color: transparent;
  color: var(--base-body);
  border: none;
  width: auto;
  min-height: unset;
  text-align: left;
  border-bottom: 1px solid transparent;
  padding: 0;
  border-radius: 0;
  font-weight: var(--link-weight);
}
.button.secondary:after, input[type=submit].secondary:after, button.secondary:after {
  display: inline;
  font-family: "Font Awesome 5 Free";
  content: "  \f054";
  font-weight: 900;
  padding: 0 5px 5px 0;
}
.button.secondary:hover, input[type=submit].secondary:hover, button.secondary:hover {
  text-decoration: none;
  border-bottom: 1px solid var(--primary-main);
  transition: border ease-in 200ms;
}
.button.full-width, input[type=submit].full-width, button.full-width {
  width: 100%;
}
.button.load-more, input[type=submit].load-more, button.load-more {
  width: 100%;
  box-sizing: border-box;
}
.button:active, input[type=submit]:active, button:active {
  transform: scale(0.95);
}

input[type=submit] {
  border-style: none;
}

a.abs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.button-wrap {
  display: inline-flex;
  width: 100%;
  flex-wrap: wrap;
}
.button-wrap.left {
  justify-content: flex-start;
}
.button-wrap.right {
  justify-content: flex-end;
}
.button-wrap.center {
  justify-content: center;
}
.button-wrap.space-around {
  justify-content: space-around;
}
.button-wrap.space-between {
  justify-content: space-between;
}
.button-wrap.space-evenly {
  justify-content: space-evenly;
}
.button-wrap.evenStretch {
  justify-content: space-evenly;
  gap: var(--s1);
}
.button-wrap.evenStretch > * {
  flex: 1 1 auto;
}

@media only screen and (max-width: 767px) {
  .fullWidthMobile {
    flex-direction: column;
    align-content: stretch;
  }
  .fullWidthMobile a:not(.secondary) {
    width: 100%;
    box-sizing: border-box;
  }
}

.box {
  /* ↓ Padding set to the first point on the modular scale */
  /* ↓ Assumes you have a --border-thin var */
  border: var(--box-border) solid;
  /* ↓ Always apply the transparent outline, for high contrast mode */
  outline: var(--box-border) transparent;
  outline-offset: calc(var(--box-border) * -1);
  border-radius: var(--box-border-radius);
}

.box > *:not(.button) {
  /* ↓ Force colors to inherit from the parent
  and honor inversion (below) */
}
.box > *:not(.button):nth-last-child(1):not(.button) {
  margin-bottom: 0;
}

.stack {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.stack > * {
  margin-top: 0;
  margin-bottom: 3px;
}
.stack > * + * {
  margin-top: var(--s1);
}
.stack-tiny > * + * {
  margin-top: 0.1em;
}

.stack-small > * + * {
  margin-top: 0.5rem;
}

.stack-large > * + * {
  margin-top: 1.2rem;
}

.stack-extra-large > * + * {
  margin-top: 2rem;
}

.green-sock {
  max-height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
}
.green-sock .grid-row {
  scroll-snap-align: start;
  height: 100vh;
}
.green-sock .grid-row .grid div {
  align-self: center;
  justify-self: center;
  text-align: center;
}